import React from 'react'
import { CustomTabs } from '../../reuseableComponents'
import EmpOverview from './EmpOverview';
import SalaryDetails from './SalaryDetails';
import InvestmentNotFound from './InvestmentNotFound';

const Investments = () => {
    const tabsRequest = [
        { name: "Overview", id: "overview" },
        { name: "Salary Details", id: "salaryDetails" },
        { name: "Investments", id: "investments" },
    ];
    const myRequestsTabs = [
        { name: "Pending", id: "pending" },
        { name: "Approved ", id: "approved" },
    ];
    return (
        <div className="cardBox  myRequest p-4">
            <div className='d-flex mb-3'>
                <div>
                    <svg
                        version="1.1"
                        id="Layer_1"
                        x={0}
                        y={0}
                        viewBox="0 0 512 512"
                        xmlSpace="preserve"
                        className="icon icon-xlg me-2"
                        style={{
                            height: "23px",
                            width: "23px",
                            cursor: "pointer",
                        }}
                    >
                        <circle id="Oval-3" opacity=".1" fill="#2B76D2" cx={256} cy={256} r={256} />
                        <path
                            fill="#2B76D2"
                            d="M277.3 362.7c-5.3 0-10.6-2-14.7-5.9l-85.3-81.5c-4.1-3.9-6.5-9.3-6.6-15-.1-5.7 2-11.1 5.9-15.2l85.3-89.1c8.1-8.5 21.7-8.8 30.2-.7 8.5 8.1 8.8 21.6.7 30.2l-70.6 73.7 69.9 66.8c8.5 8.1 8.8 21.6.7 30.1-4.2 4.4-9.8 6.6-15.5 6.6z"
                        />
                    </svg>
                </div>
                <div>
                    <h5 >
                        Sarfaraz
                    </h5>
                </div>
                <div className='ms-2 mt-1'>
                    <span className='span-label'>Emp.ID:QD2654</span>
                </div>
            </div>

            <CustomTabs tabs={tabsRequest} type="investment" ulClass={"requesttab"} />

            <div className="tab-content">
                <div className="tab-pane fade show active" id="overview">
                    <EmpOverview />
                </div>
                <div className="tab-pane fade" id="salaryDetails">
                    <SalaryDetails />
                </div>
                <div className="tab-pane fade" id="investments">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button
                                        className="nav-link active"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingTour"
                                        type="button"
                                    // onClick={() => onHandleAppliedStatus("tour", "pending")}
                                    >
                                        IT Declaration
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingTour"
                                        type="button"
                                    // onClick={() => onHandleAppliedStatus("tour", "approve")}
                                    >
                                        Proof of Investments
                                    </button>
                                </li>

                            </ul>
                            
                        </div>
                        <InvestmentNotFound/>
                    </div>

                </div>

            </div>
        </div>
    )
}

export default Investments
