import React from 'react'
import { CustomButton } from '../../globalComponents'

const InvestmentNotFound = () => {
    return (
        <div className='investment-empty'>
            <img
                src={"../../images/investmentsNotFound.svg"}
                width={100}
                className="me-2"
            />{" "}
            <div>
                <h5>POI submission is locked for this employee</h5>
                <p>You can either allow the employee to submit POI through the portal or submit the investment proofs on their behalf</p>
                <CustomButton
                    text={"Submit"}
                    className={" btn-primary"}
                />
            </div>
        </div>
    )
}

export default InvestmentNotFound
